import { VNodeFlags, ChildrenFlags } from './flags'

export const Fragment = Symbol()
export const Portal = Symbol()

export function h(tag, data = null, children = null) {
  // Á°ÆÂÆö flags
  let flags = null
  if (typeof tag === 'string') {
    flags = tag === 'svg' ? VNodeFlags.ELEMENT_SVG : VNodeFlags.ELEMENT_HTML
  } else if (tag === Fragment) {
    flags = VNodeFlags.FRAGMENT
  } else if (tag === Portal) {
    flags = VNodeFlags.PORTAL
    tag = data && data.target
  } else {
    // ÂÖºÂÆπ Vue2 ÁöÑÂØπË±°ÂºèÁªÑ‰ª∂
    if (tag !== null && typeof tag === 'object') {
      flags = tag.functional
        ? VNodeFlags.COMPONENT_FUNCTIONAL // ÂáΩÊï∞ÂºèÁªÑ‰ª∂
        : VNodeFlags.COMPONENT_STATEFUL_NORMAL // ÊúâÁä∂ÊÄÅÁªÑ‰ª∂
    } else if (typeof tag === 'function') {
      // Vue3 ÁöÑÁ±ªÁªÑ‰ª∂
      flags =
        tag.prototype && tag.prototype.render
          ? VNodeFlags.COMPONENT_STATEFUL_NORMAL // ÊúâÁä∂ÊÄÅÁªÑ‰ª∂
          : VNodeFlags.COMPONENT_FUNCTIONAL // ÂáΩÊï∞ÂºèÁªÑ‰ª∂
    }
  }

  // Á°ÆÂÆö childFlags
  let childFlags = null
  if (Array.isArray(children)) {
    const { length } = children
    if (length === 0) {
      // Ê≤°Êúâ children
      childFlags = ChildrenFlags.NO_CHILDREN
    } else if (length === 1) {
      // Âçï‰∏™Â≠êËäÇÁÇπ
      childFlags = ChildrenFlags.SINGLE_VNODE
      children = children[0]
    } else {
      // Â§ö‰∏™Â≠êËäÇÁÇπÔºå‰∏îÂ≠êËäÇÁÇπ‰ΩøÁî®key
      childFlags = ChildrenFlags.KEYED_VNODES
      children = normalizeVNodes(children)
    }
  } else if (children == null) {
    // Ê≤°ÊúâÂ≠êËäÇÁÇπ
    childFlags = ChildrenFlags.NO_CHILDREN
  } else if (children._isVNode) {
    // Âçï‰∏™Â≠êËäÇÁÇπ
    childFlags = ChildrenFlags.SINGLE_VNODE
  } else {
    // ÂÖ∂‰ªñÊÉÖÂÜµÈÉΩ‰Ωú‰∏∫ÊñáÊú¨ËäÇÁÇπÂ§ÑÁêÜÔºåÂç≥Âçï‰∏™Â≠êËäÇÁÇπÔºå‰ºöË∞ÉÁî® createTextVNode ÂàõÂª∫Á∫ØÊñáÊú¨Á±ªÂûãÁöÑ VNode
    childFlags = ChildrenFlags.SINGLE_VNODE
    children = createTextVNode(children + '')
  }

  // ËøîÂõû VNode ÂØπË±°
  return {
    _isVNode: true,
    flags,
    tag,
    data,
    children,
    childFlags,
    el: null
  }
}

function normalizeVNodes(children) {
  const newChildren = []
  // ÈÅçÂéÜ children
  for (let i = 0; i < children.length; i++) {
    const child = children[i]
    if (child.key == null) {
      // Â¶ÇÊûúÂéüÊù•ÁöÑ VNode Ê≤°ÊúâkeyÔºåÂàô‰ΩøÁî®Á´ñÁ∫ø(|)‰∏éËØ•VNodeÂú®Êï∞ÁªÑ‰∏≠ÁöÑÁ¥¢ÂºïÊãºÊé•ËÄåÊàêÁöÑÂ≠óÁ¨¶‰∏≤‰Ωú‰∏∫key
      child.key = '|' + i
    }
    newChildren.push(child)
  }
  // ËøîÂõûÊñ∞ÁöÑchildrenÔºåÊ≠§Êó∂ children ÁöÑÁ±ªÂûãÂ∞±ÊòØ ChildrenFlags.KEYED_VNODES
  return newChildren
}

export function createTextVNode(text) {
  return {
    _isVNode: true,
    // flags ÊòØ VNodeFlags.TEXT
    flags: VNodeFlags.TEXT,
    tag: null,
    data: null,
    // Á∫ØÊñáÊú¨Á±ªÂûãÁöÑ VNodeÔºåÂÖ∂ children Â±ûÊÄßÂ≠òÂÇ®ÁöÑÊòØ‰∏é‰πãÁõ∏Á¨¶ÁöÑÊñáÊú¨ÂÜÖÂÆπ
    children: text,
    // ÊñáÊú¨ËäÇÁÇπÊ≤°ÊúâÂ≠êËäÇÁÇπ
    childFlags: ChildrenFlags.NO_CHILDREN
  }
}
 
